import React, {Fragment, useState} from 'react'
import {Cell, Counter, Div, MiniInfoCell, Button, Input, FormLayout, Snackbar} from "@vkontakte/vkui";
import bridge from "@vkontakte/vk-bridge";

const Delivery = ({ deliv, sum, activePVZ, go, typeDelivery, fetchedUser, order, who }) => {
    const [street, setStreet] = useState('');
    const [house, setHouse] = useState('');
    const [room, setRoom] = useState('');
    const [snackbar, setSnackbar] = useState(null);
    const pay = () => {
        try{
            if (who === "sdek"){
                if (!activePVZ) throw 'Не выбран пункт доставки'
                if (activePVZ.name === 'Доставить домой'){
                    if (street === '') throw 'Не указан адрес доставки'
                    if (house === '') throw 'Не указан адрес доставки'
                    if (room === '') throw 'Не указан адрес доставки'
                }
            }
            if (who === "PR"){
                if (street === '') throw 'Не указан адрес доставки'
                if (house === '') throw 'Не указан адрес доставки'
                if (room === '') throw 'Не указан адрес доставки'
            }
            setSnackbar(<Snackbar
                layout="vertical"
                onClose={() => setSnackbar(null)}
            >
                <pre>
                    Заказ:
                    {JSON.stringify(order.meta, true, 2)}
                    Доставить:{`${who}  ${street} ${house} ${room}`}
                </pre>
            </Snackbar>);
            // if (fetchedUser.id){
            //     bridge.send("VKWebAppOpenPayForm", {"app_id": 7600142, "action": "pay-to-user", "params": {
            //             "amount": order.meta.sum +Math.round(Number(deliv.price)),
            //             "user_id": 199500866,
            //             "description": `Оплата товаров на сумму ${order.meta.sum +Math.round(Number(deliv.price))} в магазине "Сахарный поссум"`
            //         },
            //     });
            // }
        } catch (e) {
            setSnackbar(<Snackbar
                layout="vertical"
                onClose={() => setSnackbar(null)}
            >
                {e}
            </Snackbar>);
        }
    };

    if (deliv.error) {
        return (
            <>
                <MiniInfoCell textWrap={'full'}>{deliv.error}</MiniInfoCell>
                {/*<MiniInfoCell textWrap={'full'}>Попробуйте уменьшить количество позиций товара</MiniInfoCell>*/}
            </>
        )
    }

    return (
        <Fragment>
            <MiniInfoCell after={Math.round(Number(deliv.price))}>Стоимость доставки</MiniInfoCell>
            <MiniInfoCell after={deliv.deliveryDateMax}>Доставлено до:</MiniInfoCell>
            <MiniInfoCell after={Math.round(Number(deliv.price)) + Number(sum)}>Итого к оплате: </MiniInfoCell>
            {
                typeDelivery === 'sdek'
                && <Cell
                    expandable
                    indicator={activePVZ !== null ? activePVZ.name : 'Не выбрано' }
                    onClick={()=>go('GetPvzList')}
                >Пункт выдачи</Cell>
            }
            {
                who === 'sdek' &&
                <Cell
                    expandable
                onClick={()=>go('GetPvzList')}
                indicator={activePVZ !== null ? activePVZ.name : 'Не выбрано' }
                >Место доставки</Cell>
            }
            {
                who === 'sdek' && activePVZ !== null && activePVZ.name === 'Доставить домой' &&
                <FormLayout>
                    <Input value={street} onChange={e=>setStreet(e.target.value)} top={'Улица'}/>
                    <Input value={house} onChange={e=>setHouse(e.target.value)} top={'Дом'}/>
                    <Input value={room} onChange={e=>setRoom(e.target.value)} top={'Квартира'}/>
                </FormLayout>
            }
            {
                who === 'PR' &&
                <FormLayout>
                    <Input value={street} onChange={e=>setStreet(e.target.value)} top={'Улица'}/>
                    <Input value={house} onChange={e=>setHouse(e.target.value)} top={'Дом'}/>
                    <Input value={room} onChange={e=>setRoom(e.target.value)} top={'Квартира'}/>
                </FormLayout>
            }
            <Div>
                <Button after={<Counter>{order.meta.sum +Math.round(Number(deliv.price))}</Counter>} mode={'outline'} size={'xl'}  onClick={pay}>Оплатить</Button>
            </Div>
            {snackbar}
        </Fragment>
    )
}
export default Delivery